import React, { useState } from 'react';

const TestCreation = ({ onTestCreated, userId }) => {
  const [testConfig, setTestConfig] = useState({
    title: 'Test nomi',
    closedQuestions: 2,
    openQuestions: 1,
    duration: 150,
    startTime: '09:00',
    endTime: '11:30'
  });

  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleChange = (e) => {
    const value = e.target.type === 'number' ? parseInt(e.target.value) || 0 : e.target.value;
    setTestConfig({
      ...testConfig,
      [e.target.name]: value
    });
    if (error) setError('');
  };

  const checkTestName = async (testName) => {
    try {
      const response = await fetch(`/api/create/test/check?test_name=${encodeURIComponent(testName)}`, {
        method: 'GET',
        headers: {
          'Accept': 'application/json',
        }
      });
      
      if (!response.ok) {
        throw new Error(`Server xatosi: ${response.status}`);
      }
      
      const result = await response.json();
      return result.allowed === true;
    } catch (err) {
      console.error('Test nomini tekshirishda xato:', err);
      throw new Error('Server bilan aloqada xatolik. Iltimos, qaytadan urinib koring.');
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!testConfig.title.trim()) {
      setError('Iltimos, test nomini kiriting');
      return;
    }
    
    if (testConfig.closedQuestions + testConfig.openQuestions === 0) {
      setError('Kamida bitta savol qoshing');
      return;
    }
    
    setLoading(true);
    setError('');

    try {
      const isAllowed = await checkTestName(testConfig.title);
      
      if (!isAllowed) {
        setError('Bu nomdagi test allaqachon mavjud. Iltimos, boshqa nom tanlang.');
        setLoading(false);
        return;
      }

      onTestCreated(testConfig);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="admin-container">
      {userId && (
        <div className="user-id-info">
          Foydalanuvchi ID: {userId}
        </div>
      )}

      <h2>Test sozlash</h2>
      
      {error && <div className="error-message">{error}</div>}

      <form onSubmit={handleSubmit} className="test-config-form">
        <div className="form-group">
          <label htmlFor="title">Test nomi:</label>
          <input
            type="text"
            id="title"
            name="title"
            value={testConfig.title}
            onChange={handleChange}
            required
            disabled={loading}
          />
        </div>
        
        <div className="form-group">
          <label htmlFor="closedQuestions">Yopiq savollar soni:</label>
          <input
            type="number"
            id="closedQuestions"
            name="closedQuestions"
            min="0"
            max="20"
            value={testConfig.closedQuestions}
            onChange={handleChange}
            required
            disabled={loading}
          />
        </div>
        
        <div className="form-group">
          <label htmlFor="openQuestions">Ochiq savollar soni:</label>
          <input
            type="number"
            id="openQuestions"
            name="openQuestions"
            min="0"
            max="20"
            value={testConfig.openQuestions}
            onChange={handleChange}
            required
            disabled={loading}
          />
        </div>
        
        <div className="form-group">
          <label htmlFor="duration">Test davomiyligi (daqiqa):</label>
          <input
            type="number"
            id="duration"
            name="duration"
            min="1"
            value={testConfig.duration}
            onChange={handleChange}
            required
            disabled={loading}
          />
        </div>
        
        <div className="time-range">
          <h3>Test topshirish vaqti:</h3>
          <div className="time-inputs">
            <div className="form-group">
              <label htmlFor="startTime">Boshlanishi:</label>
              <input
                type="time"
                id="startTime"
                name="startTime"
                value={testConfig.startTime}
                onChange={handleChange}
                required
                disabled={loading}
              />
            </div>
            <div className="form-group">
              <label htmlFor="endTime">Tugashi:</label>
              <input
                type="time"
                id="endTime"
                name="endTime"
                value={testConfig.endTime}
                onChange={handleChange}
                required
                disabled={loading}
              />
            </div>
          </div>
        </div>
        
        <button 
          type="submit" 
          className="submit-btn"
          disabled={loading}
        >
          {loading ? 'Tekshirilmoqda...' : 'Javoblarni sozlash'}
        </button>
      </form>
    </div>
  );
};

export default TestCreation;
