import React, { useState, useEffect } from 'react';
import TestCreation from './components/TestCreation';
import QuestionEditor from './components/QuestionEditor';
import './App.css';

function App() {
  const [currentPage, setCurrentPage] = useState('create-test');
  const [testConfig, setTestConfig] = useState(null);
  const [userId, setUserId] = useState(null);

  useEffect(() => {
    //  user_id  URL 
    const urlParams = new URLSearchParams(window.location.search);
    const urlUserId = urlParams.get('user_id');
    
    if (urlUserId) {
      setUserId(urlUserId);
      //   localStorage   
      localStorage.setItem('userId', urlUserId);
    } else {
      //   URL  user_id,    localStorage
      const storedUserId = localStorage.getItem('userId');
      if (storedUserId) {
        setUserId(storedUserId);
      }
    }
  }, []);

  const handleTestCreated = (config) => {
    setTestConfig(config);
    setCurrentPage('edit-questions');
  };

  const handleBackToCreate = () => {
    setCurrentPage('create-test');
  };

  return (
    <div className="App">
      {currentPage === 'create-test' && (
        <TestCreation 
          onTestCreated={handleTestCreated}
          userId={userId}
        />
      )}
      {currentPage === 'edit-questions' && (
        <QuestionEditor 
          testConfig={testConfig}
          onBack={handleBackToCreate}
          userId={userId}
        />
      )}
    </div>
  );
}

export default App;
