import React, { useState, useEffect } from 'react';
import MathInput from 'react-math-keyboard';

const QuestionEditor = ({ testConfig, onBack, userId }) => {
  const [questions, setQuestions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  useEffect(() => {
    if (!testConfig) {
      onBack();
      return;
    }

    const initialQuestions = [];
    
    for (let i = 0; i < testConfig.closedQuestions; i++) {
      initialQuestions.push({
        id: i + 1,
        type: 'close',
        text: `Savol ${i + 1}: to'g'ri javobni tanlang`,
        correctAnswer: '',
        points: '1'
      });
    }
    
    for (let i = 0; i < testConfig.openQuestions; i++) {
      initialQuestions.push({
        id: testConfig.closedQuestions + i + 1,
        type: 'open',
        text: `Savol ${testConfig.closedQuestions + i + 1}: ifodani kiriting`,
        correctAnswer: '',
        points: '1'
      });
    }
    
    setQuestions(initialQuestions);
  }, [testConfig, onBack]);

  const handleAnswerChange = (questionId, answer) => {
    setQuestions(prev => 
      prev.map(q => q.id === questionId ? {...q, correctAnswer: answer} : q)
    );
  };

  const handlePointsChange = (questionId, points) => {
    const normalizedValue = points.replace(',', '.');
    setQuestions(prev => 
      prev.map(q => q.id === questionId ? {...q, points: normalizedValue} : q)
    );
  };

  const handlePointsBlur = (questionId) => {
    setQuestions(prev => 
      prev.map(q => {
        if (q.id === questionId) {
          let pointsValue = q.points;
          
          if (pointsValue === '') {
            return {...q, points: '0'};
          }
          
          pointsValue = pointsValue.replace(',', '.');
          
          if (!isNaN(pointsValue) && !isNaN(parseFloat(pointsValue))) {
            return {...q, points: parseFloat(pointsValue).toFixed(2)};
          } else {
            return {...q, points: '0'};
          }
        }
        return q;
      })
    );
  };

  const formatDateTime = (timeString) => {
    const today = new Date();
    const [hours, minutes] = timeString.split(':');
    
    today.setHours(parseInt(hours));
    today.setMinutes(parseInt(minutes));
    today.setSeconds(0);
    today.setMilliseconds(0);
    
    const year = today.getFullYear();
    const month = String(today.getMonth() + 1).padStart(2, '0');
    const day = String(today.getDate()).padStart(2, '0');
    const hoursFormatted = String(today.getHours()).padStart(2, '0');
    const minutesFormatted = String(today.getMinutes()).padStart(2, '0');
    const secondsFormatted = String(today.getSeconds()).padStart(2, '0');
    const millisecondsFormatted = String(today.getMilliseconds()).padStart(3, '0');
    
    return `${year}-${month}-${day} ${hoursFormatted}:${minutesFormatted}:${secondsFormatted}.${millisecondsFormatted}`;
  };

  const saveTestToServer = async (testData) => {
    try {
      const response = await fetch('/api/create/test', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(testData)
      });
      
      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(errorText || `Server xatosi: ${response.status}`);
      }
      
      return await response.json();
    } catch (err) {
      console.error('Testni saqlashda xato:', err);
      throw err;
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    const hasEmptyAnswers = questions.some(q => !q.correctAnswer);
    if (hasEmptyAnswers) {
      setError('Iltimos, barcha savollar uchun to\'g\'ri javoblarni kiriting');
      return;
    }

    setLoading(true);
    setError('');
    setSuccess('');

    const questionsWithNumericPoints = questions.map(q => ({
      ...q,
      points: parseFloat(q.points) || 0
    }));

    const answers = questionsWithNumericPoints.map((q, index) => ({
      answer: q.correctAnswer,
      question_type: q.type,
      question_number: index + 1,
      score: q.points
    }));
    
    const startTimeFormatted = formatDateTime(testConfig.startTime);
    const endTimeFormatted = formatDateTime(testConfig.endTime);
    
    const testData = {
      test_name: testConfig.title,
      start_time: startTimeFormatted,
      end_time: endTimeFormatted,
      test_time: testConfig.duration,
      open_questions: testConfig.openQuestions,
      close_questions: testConfig.closedQuestions,
      user_id: userId, //  user_id
      is_ended: true,  //   is_ended
      answer: answers
    };

    console.log('Yuborilayotgan test ma\'lumotlari:', testData);

    try {
      await saveTestToServer(testData);
      setSuccess('Test muvaffaqiyatli saqlandi!');
      setTimeout(() => {
        onBack();
      }, 2000);
    } catch (err) {
      setError(err.message || 'Testni saqlashda xatolik yuz berdi. Iltimos, qaytadan urinib koring.');
    } finally {
      setLoading(false);
    }
  };

  if (!testConfig || questions.length === 0) {
    return <div className="admin-container">Yuklanmoqda...</div>;
  }

  const formatDuration = (minutes) => {
    const hours = Math.floor(minutes / 60);
    const mins = minutes % 60;
    return `${hours.toString().padStart(2, '0')}:${mins.toString().padStart(2, '0')}:00`;
  };

  return (
    <div className="container">
      {userId && (
        <div className="user-id-info">
          Foydalanuvchi ID: {userId}
        </div>
      )}

      <button 
        onClick={onBack}
        className="back-btn"
        disabled={loading}
      >
         Sozlamalarga qaytish
      </button>

      <h1>{testConfig.title}</h1>
      <div className="timer">{formatDuration(testConfig.duration)}</div>
      <p className="time-range-info">
        Test {testConfig.startTime} dan {testConfig.endTime} gacha topshirilishi mumkin
      </p>

      <div className="format-hint">
        Kasr ballar uchun nuqta yoki vergul ishlating (masalan: 1.5 yoki 0,75)
      </div>

      {error && <div className="error-message">{error}</div>}
      {success && <div className="success-message">{success}</div>}

      <form id="test-form" onSubmit={handleSubmit}>
        {questions.map((question, index) => (
          <div key={question.id} className="question" data-q={index}>
            <p><strong>{index + 1}.</strong> {question.text}</p>
            
            {question.type === 'close' ? (
              <div className="admin-options">
                {['A', 'B', 'C', 'D', 'E', 'F'].map(option => (
                  <div key={option} className="admin-option">
                    <label>
                      <input
                        type="radio"
                        name={`question-${question.id}`}
                        value={option}
                        checked={question.correctAnswer === option}
                        onChange={() => handleAnswerChange(question.id, option)}
                        disabled={loading}
                      />
                      {option}
                    </label>
                  </div>
                ))}
                <div className="points-input">
                  <label>Ball:</label>
                  <input
                    type="text"
                    inputMode="decimal"
                    value={question.points}
                    onChange={(e) => handlePointsChange(question.id, e.target.value)}
                    onBlur={() => handlePointsBlur(question.id)}
                    placeholder="0.00"
                    disabled={loading}
                  />
                </div>
              </div>
            ) : (
              <div className="admin-open-question">
                <div className="math-input-container">
                  <MathInput 
                    setValue={(latex) => handleAnswerChange(question.id, latex)}
                    disabled={loading}
                  />
                </div>
                <div className="points-input">
                  <label>Ball:</label>
                  <input
                    type="text"
                    inputMode="decimal"
                    value={question.points}
                    onChange={(e) => handlePointsChange(question.id, e.target.value)}
                    onBlur={() => handlePointsBlur(question.id)}
                    placeholder="0.00"
                    disabled={loading}
                  />
                </div>
              </div>
            )}
          </div>
        ))}

        <button 
          type="submit" 
          className="submit-btn"
          disabled={loading}
        >
          {loading ? 'Saqlanmoqda...' : 'Testni saqlash'}
        </button>
      </form>
    </div>
  );
};

export default QuestionEditor;
